<template>
  <div class="container">
    <div class="row mb-3">
      <div class="col-4">
        <div class="card card-custom card-stretch">
          <div class="card-body">
            <div class="row">
              <div class="form-group col-12 mb-5">
                <span class="d-block mb-2 font-weight-bolder" for="">Pilih Divisi / AP</span>
                <v-select :disabled="false" :clearable="false" v-model="mainForm.division_id" :appendToBody="true" class="vs-style custom" :options="datasetList.division.data" :placeholder="'Pilih salah satu Divisi'">
                  <slot name="no-options">{{ $t('error.no_data') }}</slot>
                </v-select>
              </div>
              <div class="form-group col-12 mb-0">
                <span class="d-block mb-2 font-weight-bolder" for="">Tahun</span>
                <date-picker :clearable="false" :input-attr="{ autocomplete:'off' }" v-model="mainForm.periode_year" type="year" :input-class="'form-control custom-dateyears'" :value-type="formatDate('year')" :formatter="momentFormat" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col">
        <div class="card card-custom card-stretch">
          <div class="card-body py-5 pr-5">
            <div class="row no-gutters align-items-center">
              <div class="col p-0">
                <div class="form-group mb-5">
                  <span class="d-block mb-2 font-weight-bolder" for="">Pilih Agenda Input</span>
                  <v-select :disabled="false" :clearable="false" v-model="mainForm.agenda_id" :appendToBody="true" class="vs-style custom" :options="datasetList.agenda.data" :placeholder="'Pilih salah satu Agenda'">
                    <slot name="no-options">{{ $t('error.no_data') }}</slot>
                  </v-select>
                </div>
                <div class="form-group mb-0">
                  <span class="d-block mb-1 font-weight-bolder" for="">Pilih Skenario Input</span>
                  <div class="checkbox-inline position-relative py-2">
                    <b-overlay no-wrap :show="globalFilter.agenda_id === 0" rounded="sm" :opacity=".95">
                      <template #overlay>
                        <div class="d-flex flex-fill align-items-center justify-content-center font-weight-bold min-w-350px">
                          <i class="ri-lock-line ri-2x font-weight-bold text-danger"></i>
                          <span class="flex-fill pl-2">Tidak ada Skenario Input di RKAP</span>
                        </div>
                      </template>
                    </b-overlay>
                    <label v-for="(f,i) in datasetList.scenario.data" :key="i+'-form-a'" class="checkbox checkbox-lg checkbox-outline mb-1">
                      <input :disabled="globalFilter.agenda_id === 0" v-model="mainForm.scenario_input" :value="f" type="radio">
                      <span></span>{{ f.label }}</label>
                  </div>
                </div>
              </div>
              <!-- <div class="download-div ml-5 p-5 position-relative">
                <b-overlay no-wrap :show="isLoading" rounded="sm" />
                <span class="d-block mb-2 font-weight-bolder text-primary" for="">Download File Template</span>
                <p class="font-size-sm">File ini memiliki kolom heading sesuai dengan data yang diperlukan untuk mengimport data dengan benar</p>
                <button @click="downloadImport()" class="btn btn-outline-primary btn-sm font-weight-bold">
                  <i class="ri-download-2-line"></i> Download Template </button>
              </div> -->
            </div>
          </div>
        </div>
      </div>
    </div>
    <div :class="fullscreen? 'fullscreen-component': ''" class="position-relative">
      <b-overlay no-wrap :show="isLoading" rounded="sm" />
      <div class="card card-custom">
        <div class="card-header border-0 p-5" style="min-height:50px">
          <div class="d-flex flex-fill flex-wrap align-items-center justify-content-between">
            <div class="flex-fill">
            </div>
            <div class="d-flex flex-wrap align-items-center">
              <a @click="fullscreen = !fullscreen" class="btn btn-link text-primary mr-2">
                <i :class="fullscreen? 'ri-fullscreen-exit-line' : 'ri-fullscreen-line'" class="text-primary"></i> Full Screen </a>
              <!-- <div class="input-icon min-w-300px">
                <input type="text" class="form-control" v-model.trim.lazy="searchFilter" :placeholder="'Cari Data dalam '+config.title">
                <span>
                  <i class="flaticon2-search-1 icon-md"></i>
                </span>
              </div> -->
            </div>
          </div>
        </div>
        <div class="card-body px-5 pt-0 pb-0">
          <b-progress variant="success" :max="100" animated v-if="isUploading">
            <b-progress-bar :value="uploadPercentage" :label="`${uploadPercentage}%`"></b-progress-bar>
          </b-progress>
          <div v-if="uploadError || uploadLimitExceed" role="alert" class="alert alert-custom alert-light-danger py-2 px-3">
            <div class="alert-icon pr-2">
              <span class="svg-icon svg-icon-danger svg-icon-2x">
                <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                  <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                    <rect x="0" y="0" width="24" height="24"></rect>
                    <path d="M11.1669899,4.49941818 L2.82535718,19.5143571 C2.557144,19.9971408 2.7310878,20.6059441 3.21387153,20.8741573 C3.36242953,20.9566895 3.52957021,21 3.69951446,21 L21.2169432,21 C21.7692279,21 22.2169432,20.5522847 22.2169432,20 C22.2169432,19.8159952 22.1661743,19.6355579 22.070225,19.47855 L12.894429,4.4636111 C12.6064401,3.99235656 11.9909517,3.84379039 11.5196972,4.13177928 C11.3723594,4.22181902 11.2508468,4.34847583 11.1669899,4.49941818 Z" fill="#000000" opacity="0.3"></path>
                    <rect fill="#000000" x="11" y="9" width="2" height="7" rx="1"></rect>
                    <rect fill="#000000" x="11" y="17" width="2" height="2" rx="1"></rect>
                  </g>
                </svg>
              </span>
            </div>
            <div v-if="uploadError" class="alert-text"><b>Upload Gagal !</b> {{ uploadError }}</div>
            <div v-else class="alert-text"><b>Upload Gagal !</b> Ukuran File melebihi batas maksimal upload ({{uploadMax}} Mb)</div>
          </div>
          <div v-if="mainData" class="table-responsive mb-0">
            <table v-if="mainData.length" class="table table-head-custom table-bordered table-vertical-center table-input">
              <thead>
                <tr class="bg-light-info">
                  <template v-for="(th,i) in tableConfig">
                    <th v-if="!th.multiple" :class="th.class" class="text-center" style="color: #000 !important;" :key="i+'-header-table'" v-html="th.label"></th>
                    <template v-else>
                      <th v-for="(thMultiple, j) in mainData[0].data[0][th.data]" :key="j+'-'+i+'-thead-config'" :class="globalFilter.agenda_id === -Math.abs(j+1)? 'bg-primary-primary': ''" class="nowrap-table min-w-150px text-center" style="color: #000 !important;"> {{ thMultiple[th.label] | parse(th.multiple) }} <span v-if="globalFilter.agenda_id!==0" class="font-size-xs d-block text-info">{{ Math.abs(globalFilter.agenda_id) >= j+1 ? 'Actual': 'Forecast' }}</span>
                      </th>
                    </template>
                  </template>
                </tr>
              </thead>
              <tbody>
                <template v-for="(x, y) in mainData">
                  <tr :key="y+'-tr-separator'">
                    <td class="sl font-size-h4 font-weight-bolder bg-dark text-light text-italic">{{ x.label }}</td>
                    <td colspan="13" class="bg-dark"></td>
                  </tr>
                  <template v-for="(a, j) in x.data">
                    <tr :key="j+'-cf-type'" v-if="a.component_parent_id !== null && x.label === 'Cash Flow' && x.data[j> 0? j-1 : 0].cash_type !== a.cash_type">
                      <td class="text-left sl bg-light-muted text-dark text-capitalize font-weight-bolder">{{ a.cash_type? a.cash_type.split('_').join(' ') : 'Cash Type NULL' }}:</td>
                      <td :colspan="tableConfig.length + 10" class="text-left bg-light-muted text-dark"></td>
                    </tr>
                    <tr :key="y+'-'+j+'-tr-body'">
                      <template v-for="(b, k) in tableConfig">
                        <td v-if="!b.multiple" :key="j+'-'+k+'-td-input-table'" :class="[b.class || 'nowrap-table', readClass(a.level, a.type_component, a.has_backgroud)]">
                          <div v-if="b.money" class="d-flex align-items-center justify-content-between px-3">
                            <span class="pr-3">Rp.</span>
                            <span>{{ a[b.data] || 0 | parse(b.pipe) }}</span>
                          </div>
                          <template v-else> {{ a[b.data] | parse(b.pipe) }} </template>
                        </td>
                        <template v-else>
                          <td v-for="(c,l) in a[b.data]" :key="l+'-'+j+'-'+k+'-td-input-table'" class="nowrap-table" :class="[readClass(a.level, a.type_component, a.has_backgroud)]">
                            <template v-if="a.type_component === 'checker'">
                              <div class="d-block text-center">
                                <i v-if="c.value" class="flaticon2-check-mark text-success"></i>
                              </div>
                            </template>
                            <template v-else-if="a.type_component === 'percent'">
                              <div v-if="c.value" class="d-block text-right">
                                <span>{{ c.value | parse(b.pipe) }}%</span>
                              </div>
                              <div v-else class="d-block text-center">
                                <span>-</span>
                              </div>
                            </template>
                            <template v-else>
                              <div v-if="c.value" class="d-flex align-items-center justify-content-between px-0">
                                <span class="pr-3">Rp.</span>
                                <span v-if="c.value < 0">({{ c.value | parse(b.pipe) }})</span>
                                <span class="pr-2" v-else>{{ c.value | parse(b.pipe) }}</span>
                              </div>
                              <div v-else class="d-block text-center">
                                <span>-</span>
                              </div>
                            </template>
                          </td>
                        </template>
                      </template>
                    </tr>
                  </template>
                </template>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'crud-auction-plans',
  data () {
    return {
      isLoading: false,
      fullscreen: false,
      config: {
        title: 'Proses IS-BS-CF',
        permission: {
          create: 'create-auction-plans',
          read: 'view-auction-plans',
          update: 'do-verification-request-auction-plans',
          delete: 'delete-auction-plans'
        }
      },
      momentFormat: {
        stringify: (date) => {
          return date ? moment(date).format('YYYY') : ''
        },
        parse: (value) => {
          return value ? moment(value, 'YYYY').toDate() : null
        }
      },
      searchFilter: '',
      globalFilter: {},
      mainData: null,
      mainForm: {
        division_id: { label: null, code: null },
        periode_year: moment().set('date', 1).set('month', 0).format('YYYY'),
        agenda_id: { label: null, code: null },
        scenario_input: { label: null, code: null }
      },
      moduleList: [
        { code: 'custom/process-is', label: 'Income Statement' },
        { code: 'custom/process-cf', label: 'Cash Flow' },
        { code: 'custom/process-bs', label: 'Balance Sheet' }
      ],
      datasetList: {
        division: {
          api: 'divisions',
          param: { sort: 'ASC', sort_by: 'division_id' },
          option: { label: 'division_name', code: 'id' },
          data: [],
          target: 'division_id'
        },
        agenda: { api: null, param: null, option: null, data: [], target: null },
        scenario: {
          api: null,
          param: null,
          option: null,
          data: [
            { code: 'pesimist', label: 'Skenario Pesimis' },
            { code: 'moderate', label: 'Skenario Moderat' },
            { code: 'optimist', label: 'Skenario Optimis' }
          ],
          target: null
        }
      },
      isUploading: false,
      uploadMax: 1,
      uploadLimitExceed: false,
      uploadError: null,
      uploadPercentage: 0,
      tableConfig: [
        { data: 'component_name', label: 'Uraian', money: false, pipe: 'uppercase', class: 'nowrap-table sl' },
        { data: 'component_code', label: 'Kode', money: false, class: 'nowrap-table text-center', action: true },
        {
          data: 'data_months',
          label: 'periode_month',
          multiple: 'longMonth',
          money: true,
          pipe: 'number'
        }
      ]
    }
  },
  watch: {
    mainForm: {
      deep: true,
      immediate: false,
      handler (e) {
        if (e && e.division_id.code) { // hehe power of await
          this.globalFilter = Object.assign({}, this.globalFilter, {
            agenda_id: e.agenda_id.code,
            division_id: e.division_id.code,
            periode_year: moment(moment(e.periode_year, 'YYYY').toDate()).format('YYYY-MM-DD'),
            scenario_input: e.scenario_input.code
          })
        }
      }
    },
    globalFilter: {
      deep: true,
      immediate: false,
      handler (e) {
        if (e) {
          this.getMainData(e)
        }
      }
    }
  },
  mounted () {
    this.$_sys.togleClass('kt_body', 'aside-minimize', true)
    this.$store.commit('set', ['sideNavSecondary', false])
    this.getDataset()
  },
  methods: {
    readClass (level, type, bg = null) {
      let toReturn = ''
      if (level === 0) toReturn += 'font-weight-bolder font-size-lg '
      if (type === 'static' && !bg) toReturn += 'text-primary font-weight-bold '
      if (type === 'percent' || type === 'checker') toReturn += 'text-danger text-italic '
      if (bg) toReturn += 'bg-primary text-white '
      if (!bg && level === 0 && type !== 'percent' && type !== 'checker') toReturn += 'bg-light-muted text-dark'
      return toReturn
    },
    staticDataset () {
      let result = []
      for (let i = 0; i < 13; i++) {
        const e = -Math.abs(i)
        result.push({ label: i === 0 ? 'RKAP' : 'Stress Test ' + moment().set('date', 1).set('month', i - 1).format('MMMM'), code: i === 0 ? i : e })
      }
      this.$set(this.datasetList.agenda, 'data', result)
      this.$set(this.mainForm, 'agenda_id', result[0])
      this.$set(this.mainForm, 'scenario_input', this.datasetList.scenario.data[1])
    },
    async getDataset () {
      await this.staticDataset()
      for (let i = 0; i < Object.keys(this.datasetList).length; i++) {
        const e = Object.keys(this.datasetList)[i]
        if (this.datasetList[e].api) {
          this.$_api.dataset(this.datasetList[e].api, this.datasetList[e].param).then(res => {
            let result = res.data.map(x => { return { label: x[this.datasetList[e].option.label], code: x[this.datasetList[e].option.code] } })
            this.$set(this.datasetList[e], 'data', result)
            if (this.datasetList[e].target && result.length) this.$set(this.mainForm, this.datasetList[e].target, result[0])
          }, err => {
            this.$_alert.error(err)
          })
        }
      }
    },
    formatDate (type) {
      let _format
      switch (type) {
        case 'date':
        case 'date-range':
          _format = 'YYYY-MM-DD'
          break
        case 'datetime':
        case 'datetime-range':
          _format = 'YYYY-MM-DD HH:mm:ss'
          break
        case 'year':
        case 'year-range':
          _format = 'YYYY'
          break
        case 'month':
        case 'month-range':
          _format = 'YYYY-MM-DD'
          break
        case 'time':
        case 'time-range':
          _format = 'HH:mm:ss'
          break
        case 'week':
        case 'week-range':
          _format = 'DD'
          break
      }
      return _format
    },
    async getMainData (e) {
      this.isLoading = true
      let form = JSON.parse(JSON.stringify(e))
      form.scenario_input = form.agenda_id === 0 ? 'rkap' : form.scenario_input
      let resps = []
      for (let i = 0; i < this.moduleList.length; i++) {
        const e = this.moduleList[i]
        await this.$_api.dataset(e.code, form).then(res => {
          resps.push({ ...res, ...{ label: e.label } })
          this.mainData = res
        }, err => {
          this.$_alert.error(err)
        })
      }
      this.mainData = resps
      this.isLoading = false
    }
  }
}
</script>
<style scoped>
  .timeline.timeline-6:before {
    left: 1.5px !important;
  }
  .form-control.custom-dateyears {
    height: calc(1.5em + 1.19rem) !important;
  }
  .fullscreen-component {
    position: absolute !important;
    padding: 0 !important;
    top: 0;
    left: 0;
    width: 100vw !important;
    height: 100vh !important;
    z-index: 9999999999999999 !important;
    overflow-y: scroll;
  }
  .fullscreen-component .card {
    min-height: 100%;
  }
  .table-input .sr {
    position: sticky;
    right: 0; /* border */
  }
  .table-input .sl {
    position: sticky;
    left: -1px; /* border */
  }
  .table-input th.sl,
  .table-input th.sr {
    background: var(--info-light);
  }
  .table-input td.sl,
  .table-input td.sr {
    background: #fff;
  }
  .table-input td {
    padding: 3px 10px;
  }
  .download-div {
    min-width: 270px;
    max-width: 270px;
    border: 2px solid #ebedf3;
    border-radius: 8px;
  }
</style>



